import React from "react";
import { Button } from "@chakra-ui/button";
import navbar from "./navbar.css"
import { NavDropdown, Navbar, Container, Nav } from 'react-bootstrap'

export function NavbarComponent ({ 
    user
 }) {

    const addServer = () => window.open("https://discord.com/api/oauth2/authorize?client_id=903973834892382249&permissions=139589930240&scope=bot%20applications.commands", "_blank")

    const supportServer = () => window.open("https://discord.gg/Yf7rFZD9KF", "_blank")

    return (
        <div style={navbar}>
            <Navbar collapseOnSelect expand="lg" variant="dark">
                <Container>
                <Navbar.Brand href="#home">
                    <img
                    alt="Sevinco"
                    src="https://cdn.discordapp.com/attachments/747451632534683679/904995247510151198/SevincoMusic.png"
                    width="90"
                    height="72"
                    className="d-inline-block align-top"
                    />
                </Navbar.Brand>
                <Navbar.Brand href="#home" style={{fontSize:"40px"}} className="Brand">
                    Sevinco
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                    <Nav.Link onClick={
                        function () {
                            supportServer()
                        }
                    }>Support Server</Nav.Link>
                    {/* <Nav.Link href="#pricing">About</Nav.Link> */}
                    </Nav>
                    <Nav>
                        <Nav>
                            <img src={`https://cdn.discordapp.com/avatars/${user.discordId}/${user.avatar}.png?size=48`} className="avatar" alt=" " />
                        </Nav>
                    <NavDropdown title={user.discordUser} id="collasible-nav-dropdown">
                        <NavDropdown.Item href="/menu">Servers</NavDropdown.Item>
                        <NavDropdown.Item onClick={
                            function () {
                                addServer()
                            }
                        }>Add to Server</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item style={{color: "red"}} href="#action/3.4">Log Out</NavDropdown.Item>
                    </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
                </Container>
            </Navbar>
        </div>
    )
}