import React from "react";
import { Button } from 'react-bootstrap'
import styles from "./index.css"
import { CardComponent } from "..";

export function MenuComponent({ 
    guilds
 }) {

    const [hidden, setHidden] = React.useState(true)
    const addServer = () => window.open("https://discord.com/api/oauth2/authorize?client_id=903973834892382249&permissions=139589930240&scope=bot%20applications.commands", "_blank")


    return  (
        <>
            <div className="my-5">
                <h1 className="text-center fs-1 fw-bold server">Servers</h1>
            </div>
            <div className="container-fluid mb-5">
                <div className="row">
                    <div className="col-10 mx-auto">
                        <div className="row gy-4">
                            {
                                guilds.map((guild) => {
                                    return <CardComponent icon={`https://cdn.discordapp.com/icons/${guild.id}/${guild.icon}.png?size=128`} guildId={guild.id} guildName={guild.name} />
                                })
                            }
                        </div>
                    </div>
                    <div className="text-center gy-5">
                        <Button target="_blank" onClick={
                            function () {
                                addServer()
                                setHidden(false)
                            }
                        } >Add to Server</Button>

                        <p style={{color: "white", marginTop: "10px"}} hidden={hidden}>Please Refresh</p>
                    </div>
                </div>
            </div>
        </>
    )
}